@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;700;900&display=swap);
header {
	background-color: var(--mainBackground);
	text-align: center;
	top: 0;
	position: fixed;
	z-index: 100;
	width: 100%;
}

.logo {
	text-decoration: none;
	color: #fff;
}

nav {
	position: absolute;
	text-align: left;
	top: 100%;
	left: 0;
	background-color: var(--mainBackground);
	width: 100%;
	max-height: 330px;
	overflow-y: auto;
	-webkit-transform: scale(1, 0);
	        transform: scale(1, 0);
	transition: -webkit-transform 500ms ease-in-out 350ms;
	transition: transform 500ms ease-in-out 350ms;
	transition: transform 500ms ease-in-out 350ms, -webkit-transform 500ms ease-in-out 350ms;
	-webkit-transform-origin: top;
	        transform-origin: top;
}

nav ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

nav li,
nav .sublinks {
	margin-top: 0.9em;
	margin-bottom: 0.9em;
	margin-left: 1em;
}

nav .nav-link {
	color: #fff;
	text-decoration: none;
	font-size: 1.2rem;
	text-transform: uppercase;
	font-weight: bold;
	opacity: 0;
	transition: opacity 650ms ease-in-out;
}

nav .nav-link:hover {
	color: #ccc;
}

nav .desktop-link,
nav .sublinks,
input[type='checkbox'] {
	display: none;
}

.toggle-nav-label {
	position: absolute;
	top: 0;
	left: 0;
	margin-left: 1em;
	height: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
}

.toggle-nav-label .hamburger-icon,
.toggle-nav-label .hamburger-icon::before,
.toggle-nav-label .hamburger-icon::after {
	display: block;
	background-color: #fff;
	height: 5px;
	width: 2em;
	border-radius: 2px;
	position: relative;
}

.toggle-nav-label .hamburger-icon::before,
.toggle-nav-label .hamburger-icon::after {
	content: '';
	position: absolute;
}

.toggle-nav-label .hamburger-icon::before {
	bottom: 10px;
}

.toggle-nav-label .hamburger-icon::after {
	top: 10px;
}

.toggle-navbar:checked ~ nav {
	-webkit-transform: scale(1, 1);
	        transform: scale(1, 1);
}

.toggle-navbar:checked ~ nav .nav-link {
	opacity: 1;
	transition: opacity 650ms ease-in-out 550ms;
}

.toggle-navbar:checked + .toggle-nav-label .hamburger-icon {
	-webkit-animation: moveLeft 1s;
	        animation: moveLeft 1s;
	opacity: 0;
}

.toggle-navbar:checked + .toggle-nav-label .hamburger-icon::before,
.toggle-navbar:checked + .toggle-nav-label .hamburger-icon::after {
	-webkit-animation: moveRight 1s;
	        animation: moveRight 1s;
	opacity: 0;
}

@-webkit-keyframes moveRight {
	0% {
		opacity: 1;
		display: block;
	}
	5% {
		right: -10px;
	}
	50% {
		opacity: 0;
		right: 60px;
	}
}

@keyframes moveRight {
	0% {
		opacity: 1;
		display: block;
	}
	5% {
		right: -10px;
	}
	50% {
		opacity: 0;
		right: 60px;
	}
}

@-webkit-keyframes moveLeft {
	0% {
		opacity: 1;
		display: block;
	}
	5% {
		left: -5px;
	}
	50% {
		opacity: 0;
		left: 40px;
	}
}

@keyframes moveLeft {
	0% {
		opacity: 1;
		display: block;
	}
	5% {
		left: -5px;
	}
	50% {
		opacity: 0;
		left: 40px;
	}
}

.toggle-nav-label .close-nav-icon,
.toggle-nav-label .close-nav-icon::before {
	opacity: 0;
	background-color: #fff;
	height: 5px;
	width: 2em;
	border-radius: 2px;
	position: absolute;
}

.toggle-nav-label .close-nav-icon::before {
	content: '';
}

.toggle-nav-label .close-nav-icon {
	-webkit-transform: rotateZ(-45deg);
	        transform: rotateZ(-45deg);
}

.toggle-nav-label .close-nav-icon:before {
	-webkit-transform: rotateZ(90deg);
	        transform: rotateZ(90deg);
	right: 0px;
	top: 0px;
}

.toggle-navbar:checked + .toggle-nav-label .close-nav-icon {
	top: 50%;
	-webkit-animation: moveFirstToBottom;
	        animation: moveFirstToBottom;
	-webkit-animation-duration: 1s;
	        animation-duration: 1s;
	opacity: 1;
}

.toggle-navbar:checked ~ .toggle-nav-label .close-nav-icon:before {
	-webkit-animation: moveSecondToBottom;
	        animation: moveSecondToBottom;
	-webkit-animation-duration: 2s;
	        animation-duration: 2s;
	opacity: 1;
}

@-webkit-keyframes moveFirstToBottom {
	0%,
	10% {
		opacity: 0;
		top: 0;
		left: 3em;
	}
	70%,
	100% {
		left: 0;
		top: 50%;
		opacity: 1;
	}
}

@keyframes moveFirstToBottom {
	0%,
	10% {
		opacity: 0;
		top: 0;
		left: 3em;
	}
	70%,
	100% {
		left: 0;
		top: 50%;
		opacity: 1;
	}
}

@-webkit-keyframes moveSecondToBottom {
	0%,
	20% {
		top: -3em;
		opacity: 0;
	}
	50%,
	100% {
		top: 0;
		opacity: 1;
	}
}

@keyframes moveSecondToBottom {
	0%,
	20% {
		top: -3em;
		opacity: 0;
	}
	50%,
	100% {
		top: 0;
		opacity: 1;
	}
}

.toggle-navbar:not(:checked) + .toggle-nav-label .close-nav-icon:before {
	-webkit-animation: moveFirstToTop 1s;
	        animation: moveFirstToTop 1s;
	opacity: 0;
}

.toggle-navbar:not(:checked) + .toggle-nav-label .close-nav-icon {
	-webkit-animation: moveSecondToTop 2s;
	        animation: moveSecondToTop 2s;
	opacity: 0;
}

@-webkit-keyframes moveFirstToTop {
	0%,
	10% {
		top: 0;
		opacity: 1;
	}
	40%,
	100% {
		top: -3em;
		opacity: 0;
	}
}

@keyframes moveFirstToTop {
	0%,
	10% {
		top: 0;
		opacity: 1;
	}
	40%,
	100% {
		top: -3em;
		opacity: 0;
	}
}

@-webkit-keyframes moveSecondToTop {
	0%,
	20% {
		top: 50%;
		left: 0;
		opacity: 1;
	}
	40%,
	100% {
		top: 0;
		left: 3em;
		opacity: 0;
	}
}

@keyframes moveSecondToTop {
	0%,
	20% {
		top: 50%;
		left: 0;
		opacity: 1;
	}
	40%,
	100% {
		top: 0;
		left: 3em;
		opacity: 0;
	}
}

.toggle-navbar:not(:checked) + .toggle-nav-label .hamburger-icon {
	-webkit-animation: moveBackRight 1.5s;
	        animation: moveBackRight 1.5s;
	opacity: 1;
}

.toggle-navbar:not(:checked) + .toggle-nav-label .hamburger-icon::before,
.toggle-navbar:not(:checked) + .toggle-nav-label .hamburger-icon::after {
	-webkit-animation: moveBackLeft 1.5s;
	        animation: moveBackLeft 1.5s;
	opacity: 1;
}

@-webkit-keyframes moveBackLeft {
	0%,
	30% {
		right: 60px;
		opacity: 0;
	}
	70% {
		right: -10px;
		opacity: 1;
	}
	80%,
	100% {
		right: 0;
		opacity: 1;
	}
}

@keyframes moveBackLeft {
	0%,
	30% {
		right: 60px;
		opacity: 0;
	}
	70% {
		right: -10px;
		opacity: 1;
	}
	80%,
	100% {
		right: 0;
		opacity: 1;
	}
}

@-webkit-keyframes moveBackRight {
	0%,
	30% {
		left: 40px;
		opacity: 0;
	}
	70% {
		left: -5px;
		opacity: 1;
	}
	80%,
	100% {
		left: 0;
		opacity: 1;
	}
}

@keyframes moveBackRight {
	0%,
	30% {
		left: 40px;
		opacity: 0;
	}
	70% {
		left: -5px;
		opacity: 1;
	}
	80%,
	100% {
		left: 0;
		opacity: 1;
	}
}

.sublinks-toggle:checked + .sublinks {
	display: block;
}

@media (min-width: 992px) {
	.toggle-nav-label {
		display: none;
	}

	header {
		display: grid;
		grid-template-columns: 1fr auto minmax(600px, 3fr) 1fr;
	}

	.logo {
		grid-column: 2 / 3;
	}

	nav {
		all: unset;
		grid-column: 3 / 4;
		display: -webkit-flex;
		display: flex;
		-webkit-justify-content: flex-end;
		        justify-content: flex-end;
	}

	nav label {
		display: none;
	}

	nav ul {
		text-align: right;
	}

	nav ul li {
		display: inline-block;
		margin-top: 1em;
	}

	nav .nav-link {
		opacity: 1;
		margin-left: 1em;
		padding: 0.75em 0;
		position: relative;
	}

	nav .desktop-link {
		display: block;
	}

	nav .nav-link::before {
		content: '';
		display: block;
		height: 5px;
		background: #fff;
		position: absolute;
		bottom: -0.1em;
		left: 0;
		right: 0;
		-webkit-transform: scale(0, 1);
		        transform: scale(0, 1);
		transition: -webkit-transform ease-in-out 250ms;
		transition: transform ease-in-out 250ms;
		transition: transform ease-in-out 250ms, -webkit-transform ease-in-out 250ms;
	}

	nav .nav-link:hover::before {
		-webkit-transform: scale(1, 1);
		        transform: scale(1, 1);
	}

	nav ul .sublinks {
		display: block;
		position: absolute;
		top: 100%;
		border-top: 5px solid #fff;
		background-color: var(--mainBackground);
		opacity: 0;
		visibility: hidden;
		transition: top 0.3s ease-in-out, opacity 600ms ease-in-out;
	}

	nav ul li:hover > .sublinks {
		top: 60%;
		opacity: 1;
		visibility: visible;
	}

	nav ul .sublinks li {
		position: relative;
		text-align: left;
		margin: 0;
		padding: 1em 1em 1.5em;
		display: list-item;
	}
}

.section-segment {
	width: 100%;
	clear: both;
	padding: 20px;
}

.image {
	border: 5px solid var(--mainBackground);
	background-size: 110% 110%;
	background-position: 30% center;
	background-repeat: no-repeat;
	border-radius: 50%;
	width: 75%;
	padding-top: 75%;
	margin: auto;
}

.content {
	margin: 40px auto 60px;
	width: 90%;
	max-width: 700px;
	text-align: left;
}

.content p {
	font-family: times, 'Times New Roman';
	max-width: 60ch;
	font-size: 1.3em;
	font-weight: 500;
	margin-bottom: 50px;
}

.button {
	background-color: var(--mainBackground);
	color: #fff;
	border-radius: 10px;
	text-decoration: none;
	padding: 1em 1.5em;
	text-transform: uppercase;
	transition: all 300ms ease-in-out;
	font-weight: bolder;
	border: 2px solid var(--mainBackground);
}

@media (min-width: 620px) {
	.image {
		width: 50%;
		padding-top: 50%;
	}
}

@media (min-width: 992px) {
	.section-segment {
		padding: 20px 10px;
	}

	.image {
		margin: 20px 130px;
		width: 30%;
		padding-top: 30%;
	}

	.content {
		width: 40%;
		padding: 5% 2%;
	}

	.image-left,
	.content-left {
		float: left;
	}

	.image-right,
	.content-right {
		float: right;
	}

	.button-slide:hover {
		color: var(--mainBackground);
		box-shadow: inset 15em 0 0 0 #bbb;
	}
}

footer {
	background: var(--mainBackground);
	width: 100%;
	bottom: 0;
	clear: both;
}

.links {
	display: inline-block;
	width: 100%;
	text-align: center;
	margin: 10px 0;
	padding: 0 50px;
}

.footer-social-link,
.footer-link {
	color: #fff;
	padding: 0 20px;
	font-size: 1.2em;
}

.footer-link {
	display: block;
	padding: 10px 0;
	text-transform: uppercase;
	text-decoration: none;
	margin: 0 10px;
	font-weight: bolder;
}

.copyright {
	text-align: center;
	padding: 20px 0;
	color: #fff;
	font-weight: bolder;
}

@media (min-width: 992px) {
	.links {
		width: 50%;
		margin-top: 20px;
	}
	.footer-link {
		display: inline-block;
	}

	.footer-social-link {
		font-size: 1.5em;
	}
}

.contact-form {
	clear: both;
	padding: 20px;
	text-align: center;
}

.main-page {
	background-color: var(--scrollbarBackground);
	color: #fff;
}

form {
	color: #fff;
	padding: 10px 1.5em;
	background-color: var(--scrollbarBackground);
	border-radius: 10px;
	text-align: left;
	max-width: 90%;
	width: 992px;
	margin: 20px auto;
}

.main-page form {
	background-color: transparent !important;
}

form label {
	font-weight: bold;
	font-size: 1.3em;
	display: block;
	margin: 15px 0 3px;
}

form input,
form textarea {
	font-weight: 700;
	width: 100%;
	padding: 0.5em;
	font-size: 1.2em;
	border-radius: 10px;
}

form textarea {
	font-family: arial, sans-serif;
	resize: vertical;
	min-height: 200px;
	max-height: 500px;
}

form button {
	width: 100%;
	background-color: var(--mainBackground);
	color: #fff;
	font-size: 1.1em;
	border-radius: 10px;
	padding: 0.7em 1.2em;
	text-transform: uppercase;
	transition: all 500ms ease-in-out;
	font-weight: bolder;
	border: 2px solid var(--mainBackground);
}

form div {
	display: none;
}

form div.error {
	display: block;
	font-weight: bold;
	text-align: center;
	width: 100%;
	font-size: 1.2em;
	padding: 0.5em;
	margin: 10px 0;
	border: 3px solid red;
	color: red;
	border-radius: 10px;
	background-color: rgb(255, 157, 157);
}

form input.error,
form textarea.error {
	border: 3px solid red;
}

form input.error::-webkit-input-placeholder, form textarea.error::-webkit-input-placeholder {
	color: red;
}

form input.error:-ms-input-placeholder, form textarea.error:-ms-input-placeholder {
	color: red;
}

form input.error::placeholder,
form textarea.error::placeholder {
	color: red;
}

form div.success {
	display: block;
	font-weight: bold;
	text-align: center;
	width: 100%;
	font-size: 1.2em;
	padding: 0.5em;
	margin: 10px 0;
	border: 3px solid green;
	color: green;
	border-radius: 10px;
	background-color: rgb(169, 255, 169);
}

@media (min-width: 992px) {
	form button {
		width: auto;
	}

	.button-submit:hover {
		color: var(--mainBackground);
		box-shadow: inset 15em 0 0 0 #bbb;
	}
}

main {
	padding-top: 85px;
	height: 100vh;
	overflow: hidden;
	overflow-y: auto;
}


::-webkit-scrollbar {
	width: 5px;
}

::-webkit-scrollbar-track {
	border-radius: 10px;
	box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.25);
}

::-webkit-scrollbar-thumb {
	border-radius: 50px;
	height: 1%;
	background-color: #423a57c2;
	background-color: var(--scrollbarBackground);
}

*,
*::after,
*::before {
	box-sizing: border-box;
}

:root {
	--mainBackground: #423a57;
	--scrollbarBackground: #423a57c2;
}

body {
	margin: 0;
	line-height: 1.6;
	font-family: 'Work Sans', sans-serif;
	font-weight: 400;
}

